<template>
<div>
  <agriculture-header></agriculture-header>
  <div class="wrap" id="wrap">
  	<div class="logGet">
  			<!-- 头部提示信息 -->
  			<div class="logD logDtip">
  				<p class="p1">登录</p>
  			</div>
  			<!-- 输入框 -->
  			<div class="lgD">
  				<img src="../../assets/LoginName.png" width="20" height="20" alt=""/>
  				<input type="text" v-model="UserAuthVo.username"
  					placeholder="输入用户名" />
  			</div>
  			<div class="lgD">
  				<img src="../../assets/password.png" width="20" height="20" alt=""/>
  				<input type="password" v-model="UserAuthVo.password"
  					placeholder="输入用户密码" />
  			</div>
  			<div class="logC">
  				<button v-on:click="login">登 录</button>
  			</div>
  		</div>
  </div>

  <agriculture-footer></agriculture-footer>
</div>

</template>

<script>
  import AgricultureHeader from '@/components/common/AgricultureHeader.vue'
  import AgricultureFooter from '@/components/common/AgricultureFooter.vue'

  export default {
    name: 'AgricultureLogin',
    // blogHeader/blogFooter组件给申明到components里面然后在template里面使用
    components: { AgricultureHeader, AgricultureFooter },

    data () {
        return {
          UserAuthVo: { username: '', password: '' },
          // responseResult: [],  验证码使用

        }
      },
      methods: {
        login () {
          this.$axios
            .post('/login', {
              username: this.UserAuthVo.username,
              password: this.UserAuthVo.password
            })
            .then(
              successResponse => {
              this.responseResult = JSON.stringify(successResponse.data)
              if (successResponse.data.code === 200) {
                this.$router.replace({path: '/index'})
              }
            })
            .catch(failResponse => {})
        }
      },

  }

</script>

<style>

 #app .wrap{
    height: 610px;
   	width: 100;
 	  background-image: url(../../assets/LoginBackground1.jpg);
  	background-repeat: no-repeat;
    background-size: cover;
   	/* background-position: center center; */
   	position: relative;
  }
  #wrap .logGet {
  	height: 408px;
  	width: 368px;
  	position: absolute;
  	background-color: #FFFFFF;
  /*  filter:alpha(Opacity=80);-moz-opacity:0.5;opacity: 0.5; */
  	top: 30%;
  	right: 10%;
  }
  .logC button {
  	width: 100%;
  	height: 45px;
  	background-color: #ee7700;
  	border: none;
  	color: white;
  	font-size: 18px;
  }
  .logGet .logD.logDtip .p1 {
  	display: inline-block;
  	font-size: 28px;
  	margin-top: 30px;
  	width: 86%;
  }
  #wrap .logGet .logD.logDtip {
  	width: 86%;
  	border-bottom: 1px solid #ee7700;
  	margin-bottom: 60px;
  	margin-top: 0px;
  	margin-right: auto;
  	margin-left: auto;
  }
  .logGet .lgD img {
  	position: absolute;
  	top: 12px;
  	left: 8px;
  }
  .logGet .lgD input {
  	width: 100%;
  	height: 42px;
  	text-indent: 2.5rem;
  }
  #wrap .logGet .lgD {
  	width: 86%;
  	position: relative;
  	margin-bottom: 30px;
  	margin-top: 30px;
  	margin-right: auto;
  	margin-left: auto;
  }
  #wrap .logGet .logC {
  	width: 86%;
  	margin-top: 0px;
  	margin-right: auto;
  	margin-bottom: 0px;
  	margin-left: auto;
  }

</style>



